*{
  text-transform: capitalize;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.logo {
  float: left;
  margin: 0;
  padding: 20px;
  color: white;
}
.searchBtn{
  margin: -65px;
  padding: 8px 10px;
  color:#000;
  text-decoration: solid;
  border: none;
  background-color: #fff;
}
.btnn{
  margin-top:26px;
  margin-left: 30px;
  padding: 10px 10px;
  color:#000;
  text-decoration: solid;
  /* height: 30px; */
  border: none;
  background-color: #fff;
}
/* header style */
.header{
  background-color: rgb(0, 0, 0);
  height: 90px;
}
.cart-div{
  float: right;
  margin: 8px;
  position: relative;
}
.cart-div img{
  width: 80px;
}
.cart-div span{
  background-color: rgb(55, 132, 55);
  border-radius: 100%;
  height: 23px;
  width: 23px;
  position:absolute;
  top: 0px;
  right: 30px;
  font-size: 16px;
  color: #fff;
}

/* end header style */
.h1{
  margin: 5px;
}
.msg{
  color: #fff;
  background-color: rgb(195, 90, 21);
  margin-top: 3px;
  padding: 3px 67px;
  text-align: center;
  
}
.btn{
  color: black;
  margin-left: 90px;
  margin-top: 5px;
  padding : 2px 7px;
}

.btn2{
  margin: 6px;
  /* color: white; */
  padding: 4px;
  border: 1px solid;
  background-color: rgb(211, 209, 209);

}
/* product UI style */
.product-container{
  justify-content: center;
    display: flex;
    flex-wrap: wrap;
    text-decoration: none;
}
.product-item img {
  width: 220px;
}
.product-item{
  border: 1px solid #ccc;
  padding: 20px;
    margin: 10px;
    text-decoration: none;

}
/* end product Ui style */

/* cart page style */
.cart-page-container{
  display: flex;
  margin-left: 30px;
  margin-right: 30px;
}
.cart-page-container table{
  flex: 65%;
    border-collapse: collapse;
}
.cart-page-container table td{
  border: 1px solid #ccc;
  padding: 15px;
}
.price-details{
  flex: 35%;
  margin: 10px 30px;
  /* border: 2px solid ; */
}
.price-details .adjust-price{
  display: flex;
    justify-content: space-around;
    /* margin: 10px 0px; */
    padding: 10px 0px;
    border: 1px solid #ddd;
}
.price-details .adjust-price span{
  font-size: 20px;
}
#sum{
  font-weight: bold;
}
.btn3{
  padding: 10px 10px;
  color:#000;
  text-decoration: solid;
  background-color: #f2f2f2;
  /* height: 30px; */
  border: none;
}

/* end cart page style */
.search-box{
  float: left;
  width: 50%;
  padding: 25px;
  margin-left: 45px;
  position: relative;
}
.search-box input::after{
  content: none;
}
.search-box input{
  width: 100%;
    height: 30px;
    padding-left: 10px;
    
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.link{
  color: rgb(77, 74, 74);
  /* text-align: right; */
  
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


#tabl {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 50px;
  margin-top: 10px;
  text-decoration: none;
}

#tabl td, #tabl th {
  border: 2px solid rgba(0, 0, 0, 0.881);
  padding: 7px;
}

#tabl tr{background-color: #f2f2f2;}

#tabl tr:hover, .btn3:hover {background-color: #ddd;}

#tabl th {
  padding: 3px;
  /* padding-bottom: 12px; */
  text-align: center;
  background-color: rgba(0, 0, 0, 0.881);
  color: white;
  padding: 7px;
}

.product_link{
  text-decoration: none;
  color: black;
}

/* ProductDetails Page */
.container{
  display: flex;
  /* border: 2px solid grey; */
  margin: 30px;
  align-self: flex-start;

}
.left{
  flex: 30%;
  /* border: 2px solid red; */
  margin: 30px;
  padding: 10px;

}
.left img {
  width: 250px;
}
.right{
  flex: 40%;
  /* border: 2px solid fuchsia; */
  margin: 30px;
  padding: 10px;
  text-align: left;
  font-size: large;
  align-items: center;
  text-transform: capitalize;
  font-weight: 500;
}
.right div{
  padding: 10px;
  /* border-bottom: 1px solid grey; */
}
.category{
  /* margin-left: 50px; */
  padding: 10px 30px;
  color:#000;
  text-decoration: solid;
  /* height: 30px; */
  border: none;
  background-color: rgb(232, 232, 232);
  font-size: 14px;
}
.category:hover{
  background-color: rgb(195, 193, 193);
}
.category:active{
  background-color: rgb(249, 249, 249);
}

.log{
  width: 20%;
  margin: -62px 750px;
}

.dropdown {
  background-color: white;
  display: flex;
  flex-direction: column;
  border: 1px solid gray;
  margin: 10px;
  width: 94.7%;
  position: absolute;
  top: 54px;
  left: 41px;

}

.dropdown:empty {
  border: none;
}

.dropdown-row {
  cursor: pointer;
  text-align: start;
  padding-left: 3px;
  margin: 2px 0;
  font-size: 16px;
}
.dropdown-row:hover {
background-color: rgb(213, 213, 213)
}

@media (min-width: 1500px) {
  .log{
    display: none;
  }}
@media (max-width: 1200px) {
  .log{
    display: none;
  }
  .msg{
    
    display: none;
  }
  .search-box{
    width: 30%;
  }
  .logo{
    font-size: 40px;
    padding: 10px;
  }
}
@media (max-width: 600px) {
  .log{
    display: none;
  }
  .msg{
    display: none;
  }
  .search-box{
    width: 50%;
  }
.header{
  display: flex;
}
.logo{
  font-size: 24px;
}
.dropdown{
  width: 50%;
}
}